<script>
  import Vue from 'vue'
  import { mapState } from 'vuex'
  import SocketIO from "socket.io-client"
  import Config from '../config'

  let socketChat = null
  let socketGame = null

  export default {

      data(){
        return {
          msg1: "",
          warn: false,
          statusText: "...",
          timeProgress: 0,
          histories: [],
          xoc: false,
          mo: false,
          show: false,
          fields: ["username", "big", "small", "result"],
          items: [],
          d1: 1,
          d2: 2,
          d3: 3,
          selected: "auto"
        }
      },

      computed: {
        ...mapState({
              error: state => state.chats.error,
              messages: state => state.chats.messages,
              userId: state => state.auth.userId,
              name: state => state.users.name,
              errorGame: state => state.txgame.error,
              coin: state => state.txgame.coin,
              tai: state => state.txgame.tai,
              xiu: state => state.txgame.xiu,
              tongtai: state => state.txgame.tongtai,
              tongxiu: state => state.txgame.tongxiu,
              secret: state => state.txgame.secret,
              game_id: state => state.txgame.game_id,
              msg: state => state.txgame.msg,
              win: state => state.txgame.win,
              result: state => state.txgame.result,
              adminRole: state => state.users.adminRole,
              userRole: state => state.users.userRole
          })
      },

      methods: {  
          changeSelected(){
            if(this.selected == "big"){
                socketGame.emit("uResult", {uresult: 1})
            }else if(this.selected == "small"){
                socketGame.emit("uResult", {uresult: 2})
            }else{
                socketGame.emit("uResult", {uresult: 0})
            }
          },
          sendMessage(evt) {
            if(evt.which == 13){
              if(this.msg1.trim() != ""){
                socketChat.emit("send", {message: this.msg1})
                this.msg1 = ""
              }
            }
          },

          dattai(){
            this.$store.dispatch("txgame/verify")
            .then((res) => {
              if(res){
                socketGame.emit("datgame", {game: 1, cuoc: this.coin, game_id: this.game_id, secret: this.secret})
              }else{
                Vue.$toast.open({
                    message: this.errorGame,
                    type: 'error'
                });
              }
            })
          },

          datxiu(){
             this.$store.dispatch("txgame/verify")
            .then((res) => {
              if(res){
                socketGame.emit("datgame", {game: 2, cuoc: this.coin, game_id: this.game_id, secret: this.secret})
              }else{
                Vue.$toast.open({
                    message: this.errorGame,
                    type: 'error'
                });
              }
            })
          },

          updateCoin1() {
            this.$store.dispatch("txgame/selectCoin", {coin: 100})
          },

          updateCoin2() {
            this.$store.dispatch("txgame/selectCoin", {coin: 200})
          },

          updateCoin3() {
            this.$store.dispatch("txgame/selectCoin", {coin: 500})
          },

          updateCoin4() {
            this.$store.dispatch("txgame/selectCoin", {coin: 1000})
          },

          updateCoin5() {
            this.$store.dispatch("txgame/selectCoin", {coin: 2000})
          },

          updateCoin6() {
            this.$store.dispatch("txgame/selectCoin", {coin: 5000})
          },
      },

      created(){
          socketChat = SocketIO(Config.BASE_URL + "/chat", {query: {token: localStorage.getItem("token")}})
          socketGame = SocketIO(Config.BASE_URL + "/game", {query: {token: localStorage.getItem("token")}})
          this.$store.dispatch('users/getUser', {id: localStorage.getItem("userId")})
      },

      mounted() {
          this.$store.dispatch("auth/clearError")
          this.$store.dispatch("chats/loadMessages")
          .then(()=> {
             let container = this.$el.querySelector("ul.chat-ul");
              container.scrollTop = container.scrollHeight;
          })
          socketGame.on("history", (data) => {
            this.histories = data.histories
          })
          socketChat.on("message", (data) => {
            this.$store.dispatch("chats/addMessage", data)
            .then(() => {
              let container = this.$el.querySelector("ul.chat-ul")
              container.scrollTop = container.scrollHeight
            })
          })
          socketGame.on("game_ready", (data) => {
            this.xoc = true
            this.mo = false
            this.show = false
            this.selected = "auto"
            setTimeout(() => {
              this.xoc = false
              this.mo = false
              this.show = false
            }, 2000)
            this.statusText = "Betting"
            this.warn = false
            this.$store.dispatch("txgame/updateGameInfo", {secret: data.secret, game_id: data.game_id})
            .then(() => {
              Vue.$toast.open({
                    message: "Bat dau dat cuoc",
                    type: 'warning'
                });
            })
          })
          socketGame.on("xacnhan_datgame", (data) => {
            this.$store.dispatch("txgame/confirmUser", data)
          })
          socketGame.on("total_game", (data) => {
            this.$store.dispatch("txgame/confirmTotal", data)
            this.items = data.items
          })
          socketGame.on("timer", (data) => {
            this.statusText = "Betting"
            this.timeProgress = data.timer
          })
          socketGame.on("game_win", (data) => {
           
            this.$store.dispatch("txgame/updateWin", {win: data.value})
            Vue.$toast.open({
                message: "Thang cuoc " + data.value + " Z",
                type: 'success'
            });
            this.$store.dispatch("txgame/updateBalance", {balance: data.balance})
          })
          socketGame.on("game_lose", (data) => {
            
            this.$store.dispatch("txgame/updateWin", {win: -data.value})
            Vue.$toast.open({
                message: "Thua cuoc " + data.value + " Z",
                type: 'error'
            });
          })
          socketGame.on("items", (data) => {
            this.items = data.items
          });
          socketGame.on("game_result", (data) => {
            this.xoc = false
            this.mo = true
            this.show = true
            this.d1 = data.d1
            this.d2 = data.d2
            this.d3 = data.d3
            setTimeout(() => {
              this.xoc = false
              this.mo = false
            }, 2000)
            this.timeProgress = 0
            this.histories = data.histories
            if(data.result == 1){
              this.statusText = "Result: Big"
            }else{
              this.statusText = "Result: Small"
            }
            this.$store.dispatch("txgame/updateResult", data)
          })
          socketGame.on("error1", (data) => {
            Vue.$toast.open({
                message: data.msg,
                type: 'error'
            });
          })
      },

      beforeDestroy(){
        socketChat.disconnect();
        socketGame.disconnect();
      }
  }
</script>

